import React from 'react'
import { Header } from './Header'

import Particles from 'react-particles-js'


const Home = () => {
    return (
        <div>        
           <Header/>
            <Particles
                  params={{
                    "particles": {
                        "number": {
                            "value": 50
                        },
                        "size": {
                            "value": 6
                        }
                    },
                    "interactivity": {
                        "events": {
                            "onhover": {
                                "enable": true,
                                "mode": "repulse"
                            }
                        }
                    }
                }} 
            />
           
        </div>
    )
}

export default Home
